import axios from 'axios'

const sentData =async (obj) => {
    let result, results;
    let { usercode, password } = JSON.parse(localStorage.getItem("userfrom"));
    let from = {
        fwtype: 'YD',
        datatype: "HQCK",
        usercode: usercode,
        password: password,
        orgdata: JSON.stringify(obj)
    };
    let tbfrom = {
        fwtype: 'YD',
        datatype: "TBCX",
        usercode: usercode,
        password: password,
        orgdata: JSON.stringify(obj)
    };
    try {
        result = await axios.post("/lierutil", from)
        results = await axios.post("/lierutil", tbfrom)
        console.log(result, results)
        localStorage.setItem("HQCK");
        localStorage.setItem("TBCX");

    } catch (e) {
        console.log(e)
    }
    return result, results;

}
export { sentData }