import axios from 'axios'
const mxcx =async (data) =>{
    console.log(data)
    let { usercode, password } = JSON.parse(localStorage.getItem("userfrom"));
    let { orgdata } = JSON.parse(localStorage.getItem("HQCK"));
    let result;
    let from ={
        fwtype: "YD",
        datatype: "MXCX",
        usercode,
        password,
        orgdata,
        ...data
    }
    for (var key in from) {
        if (String(from[key]) == "true") {
            from[key] = "Y";
        } else if (String(from[key]) == "false") {
            from[key] = "N";
        }
      }
    try{
        result=   await axios.post('/lierutil',from)
        console.log(result)
        localStorage.setItem("MXdata",result.data);
    }catch(e){
        console.log(e)
    }
return result;
}
export {
    mxcx
}