<template>
  <div class="Echarts">
    <div id="Echarts1"></div>
  </div>
</template>
<script>
export default {
  name: "Echarts1",
  data() {
    return {
      tbdata: {
        jczzdybl: "100",
        kcppddybls: "0",
        jczzdymbkcbl: "0",
        orgdata: ""
      }
    };
  },
  created() {
    let data = JSON.parse(localStorage.getItem("TBCX"));
    this.tbdata = data;
  },
  methods: {
    Echarts1() {
      let Echarts1 = this.$echarts.init(document.getElementById("Echarts1"));
      let option = {
        title: {
          text: "库存匹配度",
          subtext: "百分比",
          left: "left",
          textStyle: {
            //主标题文本样式{"fontSize": 18,"fontWeight": "bolder","color": "#333"}
            fontFamily: "PingFang-SC-Regular",
            fontSize: 14,
            fontStyle: "normal",
            fontWeight: "normal",
            color: "#00a0e9"
          },
          subtextStyle: {
            //副标题文本样式{"color": "#aaa"}
            fontFamily: "PingFang-SC-Regular",
            fontSize: 14,
            fontStyle: "normal",
            fontWeight: "normal",
            color: "#565656"
          }
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          left: "right",
          data: ["库存匹配度>1.2", "库存匹配度<0.8", ">0.8并<=1.2"]
        },
        series: [
          {
            name: "库存匹配度",
            type: "pie",
            radius: "55%",
            center: ["50%", "62%"],
            data: [
              {
                value: this.tbdata.kcppddybl,
                name: "库存匹配度>1.2",
                itemStyle: {
                  color: "#00a0e9"
                }
              },
              {
                value: this.tbdata.kcppdxybl,
                name: "库存匹配度<0.8",
                itemStyle: {
                  color: "#fff100"
                }
              },
              {
                value: this.tbdata.kcppddybls,
                name: ">0.8并<=1.2",
                itemStyle: {
                  color: "#ffe488"
                }
              }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };
      Echarts1.setOption(option);
    },
    getwidth(res) {
      let docEl = document.documentElement,
        clientWidth =
          window.innerWidth ||
          document.documentElement.clientWidth ||
          document.body.clientWidth;
      if (!clientWidth) return;
      let fontSize = 100 * (clientWidth / 1920);
      return res * fontSize;
    }
  },
  mounted() {
    this.Echarts1();
  }
};
</script>
<style lang="less">
.Echarts {
  width: 100%;
  border: solid 1px #00a0e9;
  background-color: #fff;
  border-radius: 10px;
}
#Echarts1 {
  width: 100%;
  height: 260px;
}
</style>