<template>
  <div id="cxtj">
    <div class="cxtj-li">
      <div class="cxtj-li-center">
        <li
          v-for="(item,index) in checkboxGroup1"
          :key="index"
          :class="{checked:senddata.includes(item)}"
          @click="ClickActive(item,index)"
        >{{item.ckname}}</li>
      </div>
    </div>
    <div class="cxtj-main">
      <div>
        <li :class="{checked:isjczzdymbkc}" @click="isjczzdymbkc=!isjczzdymbkc">周转天数大于目标库存天数</li>
        <li :class="{checked:isjczzxyaqkc}" @click="isjczzxyaqkc=!isjczzxyaqkc">周转天数小于安全库存天数</li>
      </div>
      <div>
        <li :class="{checked:iskcppddy}" @click="iskcppddy=!iskcppddy">库存匹配度大于1.2</li>
        <li :class="{checked:iskcppdxy}" @click="iskcppdxy=!iskcppdxy">库存匹配度小于0.8</li>
      </div>
    </div>
    <div class="cxtj-bottom">
      <el-button type="primary" plain size="mini" @click="qxbutton">全选</el-button>
      <el-button type="primary" plain size="mini" @click="noqxbutton">取消</el-button>
      <div id="send">
        <el-button type="primary" size="mini" @click="send">查询</el-button>
      </div>
    </div>
  </div>
</template>
<script>
import {mxcx} from '@/api/cxtj'
export default {
  name: "cxtj",
  data() {
    return {
      checkboxGroup1: [
        { ckname: "1公司", ckcode: "1" },
      ],
      xzclass: "xz",
      activeIndex: 0,
      noclass: "noxz",
      isjczzdymbkc: false,
      isjczzxyaqkc: false,
      iskcppddy: false,
      iskcppdxy: false,
      cities: [],
      message: "",
      senddata: []
    };
  },
  watch:{
     isjczzdymbkc: function(x) {
      if (x) {
        this.isjczzxyaqkc = false;
      }
    },
    isjczzxyaqkc: function(x) {
      if (x) {
        this.isjczzdymbkc = false;
      }
    },
    iskcppddy: function(x) {
      if (x) {
        this.iskcppdxy = false;
      }
    },
     iskcppdxy: function(x) {
      if (x) {
        this.iskcppddy = false;
      }
    }
  },
  methods: {
    qxbutton() {
      this.senddata = this.checkboxGroup1;
    },
    noqxbutton() {
      this.senddata = [];
    },
    ClickActive(x, y) {
      if (this.senddata.includes(x)) {
        this.senddata = this.senddata.filter(ele => ele != x);
      } else {
        this.senddata.push(x);
      }
    },
    first() {
     this.checkboxGroup1=JSON.parse(localStorage.getItem("HQCK")).ckdata
    },
    send() {
       let from = {
          isjczzdymbkc: this.isjczzdymbkc,
          isjczzxyaqkc: this.isjczzxyaqkc,
          iskcppddy: this.iskcppddy,
          iskcppdxy: this.iskcppdxy,
          ckdata: this.senddata
     }
     mxcx(from).then(res=>{
       this.$router.push('table')
     })

    }
  },
  mounted() {
    this.first();
  }
};
</script>
<style lang="less" scoped>
#cxtj {
  width: 100%;
  height: 92%;
  background-color: #fff;
}
#cxtj .cxtj-li {
  width: 100%;
  max-height: 70%;
  display: flex;
  justify-content: center;
  .cxtj-li-center {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    flex-direction: row;
    justify-content: center;
    overflow-y: scroll;
    li {
      width: 75px;
      height: 30px;
      border: 1px solid #00a0e9;
      margin: 6px;
      border-radius: 2px;
      line-height: 30px;
      p {
        color: #333333;
      }
    }
  }
}
.xz {
  background: #00a0e9;
}

li.checked {
  color: #fff;
  background: #00a0e9;
  transition: all 0.3s linear;
}

#cxtj .cxtj-main {
  width: 100%;
  padding-top: 20px;
  display: flex;
  div {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    li {
      width: 90%;
      height: 30px;
      border: 1px solid #00a0e9;
      margin: 6px;
      border-radius: 2px;
      line-height: 30px;
    }
  }
}
#cxtj .cxtj-bottom {
  padding-top: 20px;
  width: 100%;
}
#cxtj #send {
  padding-top: 20px;
}
</style>