<template>
  <div class="Echarts">
    <div id="echartszc"></div>
  </div>
</template>
<script>
export default {
  name: "echartszc",
  data(){
      return {
          tbdata:{
            'jczzxyaqkcbl':'100',
            "jczzdybl":"100",
            "kcppddybls":"0",
            "jczzdymbkcbl":"0",
            "orgdata":"",
          }
      }
  },
  methods: {
    Echarts1() {
     let data = JSON.parse(localStorage.getItem("TBCX"));
     this.tbdata = data;
      console.log(this.tbdata.jczzdyaqkcbl)
      let Echarts1 = this.$echarts.init(document.getElementById("echartszc"));
      let option = {
        title: {
          text: "库存匹配度",
          subtext: "百分比",
          left: "left",
          textStyle: {
            //主标题文本样式{"fontSize": 18,"fontWeight": "bolder","color": "#333"}
            fontFamily: "PingFang-SC-Regular",
            fontSize: 14,
            fontStyle: "normal",
            fontWeight: "normal",
            color: "#00a0e9"
          },
          subtextStyle: {
            //副标题文本样式{"color": "#aaa"}
            fontFamily: "PingFang-SC-Regular",
            fontSize: 14,
            fontStyle: "normal",
            fontWeight: "normal",
            color: "#565656"
          }
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          left: "right",
          data: [
            "周转天数>目标库存天数","周转天数<安全库存天数","周转天数>=等于安全库存天数并且<=等于目标库存天数"],
        },
        series: [
          {
            name: "结存周转天数",
            type: "pie",
            radius: "55%",
            center: ["50%", "62%"],
            data: [
              {
                value: this.tbdata.jczzdymbkcbl,
                name: "周转天数>目标库存天数",
                itemStyle: {
                  color: "#00a0e9"
                }
              },
              {
                 value: this.tbdata.jczzxyaqkcbl,
                name: "周转天数<安全库存天数",
                itemStyle: {
                  color: "#fff100"
                }
              },
               {
                value: this.tbdata.jczzdybl,
                name: "周转天数>=安全库存天数并且<=目标库存天数",
                itemStyle: {
                  color: "#ffe488"
                }
              }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };
      Echarts1.setOption(option);
    }
  },
  mounted() {
    this.Echarts1();
  }
};
</script>
<style lang="less">
.Echarts {
  width: 100%;
  border: solid 1px #00a0e9;
  background-color: #fff;
  border-radius: 10px;
}
#echartszc {
  width: 100%;
  height: 260px;
}
</style>