<template>
  <div class="Company">
    <ul class="Company-ul">
      <li v-for="(item,index) in options " :key="index">
        <label>
          <input type="checkbox" class="inputcheckbox" @click="hx(item)" />
          <span></span>
          <p>{{item.orgname}}</p>
        </label>
      </li>
      <el-button
        type="success"
        icon="el-icon-check"
        class="Company-button"
        circle
        @click="senddata()"
      ></el-button>
    </ul>
  </div>
</template>
<script>
import { sentData } from "@/api/company";
export default {
  name: "Company",
  data() {
    return {
      options: [],
      orgdata: [],
      loginForm: {
        fwtype: "YD",
        datatype: "YHYS",
        wxusercode: "",
        wxusername: ""
      },
      qqForm: {
        fwtype: "访问类型",
        datatype: "YHYS",
        wxusercode: "微信账户编码",
        wxusername: "微信账户名称"
      },
      logindata: {
        fwtype: "YD",
        datatype: "YHYS",
        usercode: "NC账户编码",
        username: "NC账户名称"
      },
      userfrom: {
        fwtype: "YD",
        datatype: "YHYZ",
        usercode: "",
        password: ""
      }
    };
  },
  watch: {
    /*  'value':function(){
        this.$router.push('navtwo');
        this.$axios.get('/api').then(res=>{
          console.log(res.data)
        })
      } */
  },
  methods: {
    //拿取数据
    logindatata() {
      //先从local里面取出数据
      this.qqForm = JSON.parse(localStorage.getItem("qqForm"));
      this.$axios.post("/lierutil", this.qqForm).then(res => {
        if (res.status == 200) {
          //
          this.logindata = res.data;
          this.userfrom.usercode = this.logindata.usercode;
          localStorage.setItem("userfrom", JSON.stringify(this.userfrom));
        } else {
          alert(res.message);
        }
      });
      //用户验证
      this.$axios.post("/lierutil", this.userfrom).then(res => {
        if (res.status == 200) {
          localStorage.setItem("logindata", JSON.stringify(res.data));
        } else {
          alert("服务器繁忙");
        }
      });
      this.options = JSON.parse(localStorage.getItem("logindata")).orgdata;
    },
    //获取选中的数组
    hx(a) {
      let targ = this.orgdata.findIndex(item => item.orgcode == a.orgcode);
      if (targ >= 0) {
        this.orgdata.splice(targ, 1);
      } else {
        this.orgdata.push(a);
      }
    },
    async senddata() {
      sentData(this.orgdata);
      this.$router.push("navtwo");
    }
  },
  created() {
    this.logindatata();
  },
  mounted() {}
};
</script>
<style lang="less" scoped>
.Company {
  width: 100%;
  height: 100%;
  padding: 15px;
  font-family: "SourceHanSansCN-Normal";
  .Company-title {
    width: 100%;
    height: 40px;
    background-color: #fff;
    border-radius: 4px;
    border: 1px solid #2194e1;
    padding-left: 10.5px;
    p {
      font-size: 15px;
      line-height: 40px;
      text-align: left;
    }
  }
  .Company-ul {
    width: 100%;
    height: 100%;
    margin-top: 10px;
    padding-top: 15px;
    padding-bottom: 200px;
    background: #fff;
    overflow-y: scroll;
    border-radius: 4px;
    li {
      width: 100%;
      height: 38px;
      padding-left: 15px;
      label {
        position: relative;
        width: 100%;
        height: auto;
        display: inline-block;
        display: flex;
        justify-content: left;
        line-height: 38px;
      }
      //选择框样式
      .inputcheckbox {
        display: none;
      }
      .inputcheckbox + span {
        width: 13px;
        height: 13px;
        margin-top: 12px;
        border: 1px solid #ccc;
        display: inline-block;
        border-radius: 2px;
      }
      .inputcheckbox:checked + span {
        position: relative;
      }
      .inputcheckbox:checked + span::after {
        content: "\2714";
        color: #2194e1;
        position: absolute;
        top: -13px;
        left: 0;
        font-size: 13px;
      }
      p {
        font-size: 14px;
        line-height: 38px;
        margin-left: 20px;
        text-align: left;
        color: rgba(64, 64, 64, 1);
      }
    }
  }
}
.Company-button {
  position: fixed;
  bottom: 20px;
}
</style>