import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/login.vue'

import Navtwo from '../views/navtwo.vue'
import SelectCompany from '../views/SelectCompany.vue'
import Table from '../views/table.vue'
import Echart from '@/views/echarts'
import Bzt from '@/views/bzt'
import Cxtj from '@/views/cxtj'
import toppage from '@/views/top'

Vue.use(VueRouter)

  const routes = [
 /*  {path:'*',name:'/404',component:Nofind}, */
  { path: '/', redirect: '/login' },
  { path: '/Login', name: 'login', component: Login },
  { path: '/toppage', name: 'toppage', component: toppage,children:[
    { path: '/navtwo', name: 'navtwo', component: Navtwo },
    { path: '/Company', name: 'Company', component: SelectCompany },
    { path: '/Table/', name: 'Table', component: Table},
    { path: '/Echart/', name: 'Echart', component: Echart},
    { path: '/Bzt', name: 'Bzt', component: Bzt},
    { path: '/Cxtj', name: 'Cxtj', component: Cxtj},
  ]}
  
]

const router = new VueRouter({
  //mode: 'history',
  base: process.env.BASE_URL,
  routes
})

