<template>
  <el-table
    :data="tableData"
    :row-class-name="tableRowClassName"
    height="100%"
    size="mini"
    border
    style="width: 100%"
  >
    <el-table-column prop="ckname" label="仓库名称" width="80"></el-table-column>
    <el-table-column prop="chname" label="存货名称" width="80"></el-table-column>

    <el-table-column prop="scs" label="生产商" width="80"></el-table-column>

    <el-table-column prop="jcfnum" label="结存数量" width="80"></el-table-column>
    <el-table-column prop="ryl" label="日用量" width="80"></el-table-column>
    <el-table-column prop="jczzts" label="结存周转天数" width="100"></el-table-column>
    <el-table-column prop="aqkcts" label="安全库存天数" width="100"></el-table-column>
    <el-table-column prop="mbkcts" label="目标库存天数" width="100"></el-table-column>
    <el-table-column prop="kcppd" label="库存匹配度" width="100"></el-table-column>
    <el-table-column prop="fhfnum7" label="7天内发货数" width="100"></el-table-column>
  </el-table>
</template>

<script>
export default {
  name: "Table",
  data() {
    return {
      tableData: [
      
      ]
    };
  },
  methods: {
    getMXDATA() {
      let data = JSON.parse(localStorage.getItem("MXdata"));
      this.tableData = data.data;
    },
    tableRowClassName({ row, rowIndex }) {
    //  console.log(row, rowIndex);
      if (row.kcppd > 1.2) {
        return "warning-row";
      } /*  else if (rowIndex === 3) {
          return 'success-row';
        }
        return ''; */
    }
  },
   mounted() {
 this.getMXDATA();
  }
};
</script>
<style lang="less">
.el-table .warning-row {
  background: #e4d700;
}

.el-table .success-row {
}
</style>