<template>
    <div class="toppage">
        <div class="topnav">
            <div class="topnavimg" @click="gohome"></div>
            <p>{{toptitle}}</p>
        </div>
       <router-view/>

    </div>
</template>

<script>
  export default {
      name:'toppage',
      data(){
          return{
              toptitle:'公司设置'
          }
      },
      watch:{
          $route:function(to,from){
              this.change(to);
              
          }
      },
      methods:{
          change(to){
              switch (to.name){

                  case 'Company':
                      this.toptitle = '公司设置'
                      break;
                case 'navtwo':
                        this.toptitle = '选择图表'
                break;
                case 'Table':
                        this.toptitle = '库存分析明细表'
                break;
                case 'Bzt':
                        this.toptitle = '饼状图'
                break;
                case 'Cxtj':
                     this.toptitle = '查询条件'
                     break;
              }
          },
          gohome(){
              this.$router.go(-1);
          }
      }
  }
</script>
<style lang="less">
    .toppage{
        width: 100%;
        height: 100%;
        overflow: hidden;
       
    }
   .toppage .topnav{
       width: 100%;
        height: 8%;
         background-image: -moz-linear-gradient(left top, #2194e1, #64bef8);
            background-image: -webkit-linear-gradient(left top,  #2194e1, #64bef8);
            position: relative;

   }
    .toppage .topnav .topnavimg{
        position: absolute;
        left:4%;
        top: 50%;
        margin-top: -10px;
        content: "";
        display: inline-block;
        height: 14px;
        width: 14px;
        border-width:  0 0 2px 2px;
        border-color: #fff;
        border-style: solid;
        transform: matrix(0.71, 0.71, -.71, 0.71, 0, 0);
        -webkit-transform: matrix(0.71, 0.71, -.71, 0.71, 0, 0);

    }
     .toppage .topnav p{
        position: absolute;
        min-width: 100px;
        left:50%;
        top: 50%;
        margin-top: -9px;
        margin-left: -50px;
         font-size: 18px;
         color: #fff;
     }
</style>